@import url("https://fonts.googleapis.com/css?family=Yellowtail&display=swap");

$main_font_size: calc(2vw + 2vh);
$main_underline: calc(2vw + 2vh + 1rem);
html,
body {
  height: 100%;
  font-size: 62.5%;
  font-family: "Yellowtail";
  color: #222222;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.title {
  font-weight: bold;
  font-size: $main_font_size;
  padding: calc(2vw + 1vh);
  margin: auto;

  @keyframes blink {
    0% {
      opacity: 0.5;
    }
    30% {
      opacity: 1;
    }
    70% {
      opacity: 1;
    }
    100% {
      opacity: 0.5;
    }
  }
}
.term {
  color: #222222;
  font-size: calc(3vw + 1.5vh);
}

.button {
  &__start {
    font-weight: 700;
    font-size: calc(2vw + 1vh);
    color: #ccc;
    text-transform: uppercase;
    text-decoration: none;
    background: white;
    margin-top: 2rem;
    padding: 2rem;
    border: none;
    &:hover {
      color: #494949;
    }
  }
}
.input {
  font-family: "Yellowtail";
  border: none;
  font-size: $main_font_size;
  padding-bottom: 1.5rem;

  outline: none;
  text-align: center;
  background-image: linear-gradient(to bottom, #ccc 75%, transparent 100%);
  background-position: 0 $main_underline;
  background-repeat: repeat-x;
  background-size: 0.3rem 0.3rem;
  &::placeholder {
    color: #ccc;
  }
  &__title {
    width: calc(25vw + 25vh);
  }
  &__minute {
    width: calc(10vw + 10vh);
  }
}
